import React, {PropTypes} from 'react';
import KlassName from 'teapan/klassName';

class Layout extends React.Component {
	constructor(props) {
		super(props);
	}

	render() {
		const props = this.props;

		let {
			advanced,
			className,
			children,
			...others
		} = props;

		const klassName = KlassName(
			'layout',
			{
				['advanced']:advanced,
			},
			className
		);

		return (
			<div
				{...others}
				className={klassName}
			>
				{children}
			</div>
		);
	}
};

Layout.propTypes = {
	advanced: PropTypes.bool,

	className: PropTypes.string,
	children: PropTypes.node,
};

Layout.defaultProps = {
	advanced: false,
};

export default Layout;